body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  height: 100%;
  /* height: auto; */
  box-sizing: border-box;

}
h1{
  box-sizing: border-box;
  font-family: 'Nighty DEMO';
  font-size: 5rem;
  margin-bottom: 0;
  color: gainsboro;
  letter-spacing: 6px;

}
body{
  background-image: linear-gradient(to bottom, rgb(105, 85, 255), rgb(111, 144, 153));
}
input{
  width: 50%;
  margin-bottom: 2%;
}
.loader{
  margin: auto auto;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

